<template>
    <div class="wiki-publish-article">
        <div class="wiki-publish-article-block">
            <div>
                <span class="demonstration">请选择发表栏目:</span>
                <el-cascader
                    expand-trigger="hover"
                    :options="options"
                    v-model="selectedOptions"
                >
                </el-cascader>
            </div>
        </div>

        <div class="wiki-publish-article-title">
            <el-input
                type="text"
                placeholder="请输入标题"
                v-model="text"
                maxlength="15"
                show-word-limit
            >
            </el-input>
        </div>
        
        <div id="main">
            <mavon-editor v-model="html"/>
            <el-button type="primary" @click="submission" >提交</el-button>
        </div>
        
        
    </div>
</template>showdown

<script>
import * as ajax from '@/api'

export default {
  data () {
    return {
        html: '',
        selectedOptions:'',
        options: [],
    }
  },
  created(){
    this.html = this.$route.query.data.content
    this.text = this.$route.query.data.title
    ajax.getOutlineList().then(resp=>{
        let list = resp.data.data.article_category
        console.log(this.$route.query.data)
        this.selectedOptions = [this.$route.query.data.type_id,this.$route.query.data.id]
        list.forEach(element => {
            element['value'] = element['id']
            element['label'] = element['type_name']
            element['children'] = element['all_book']
            element['children'].forEach(item=>{
                item['value'] = item['id']
                item['label'] = item['book_name']
            })
        });
        this.options = list
    })
  },
  methods:{
      submission(){
          if(confirm("确定提交吗？")){
          }else{
          }
      },
      submission(){
          console.log(this.selectedOptions)
          if(!this.text){
              alert('标题不能为空')
          }else if(!this.selectedOptions){
              alert('请选择发表栏目')
          }else if(!this.html){
              alert('文章不能为空')
          }else{
            ajax.modifyArticleContent(
                this.$route.query.data.id, //文章id
                '', //用户
                this.selectedOptions[1],   //文章所属大纲id
                this.$route.query.data.status_id, //文章状态
                this.text,  //文章标题
                this.html, //文章内容
                '','',   //阅读量和评论数
                this.selectedOptions[0] //类型id
                ).then(resp=>{
                if(resp.data.code){
                    this.$message({
                        message: '修改成功',
                        type: 'success'
                    });
                }else{
                    this.$message.error(resp.msg);
                }
            })
          }
      }
  },

}
</script>

<style lang='scss'>
    .wiki-publish-article{
        position: relative;
        min-height: 100%;
        width: 100%;
        &-text{
          width: 100%;
          height: auto;
          padding: 30px;
        }
        &-title{
            width: 100%;
            margin-bottom: 20px;
            .el-input{
                width: 1280px;
                margin: 0 auto;
                margin-top: 30px;
                display: block;
            }      
        }
        &-block{
                margin: 20px 0 20px 0;
                >div{
                width: 1280px;
                margin: 0 auto;
                .demonstration{
                    margin-right: 20px;
                }
                .el-input--suffix{
                    width: 600px;
                }
            }
            
        }

        .el-button{
            width: 100px;
            height: 30px;
            line-height: 9px;
            font-size: 12px;
            text-align: center;
        }
        .el-button--primary{
            position: absolute;
            right: 589px;
            bottom: -43px;
        }
        #main{
            width: 1280px;
            margin: 0 auto;
            height: 900px;
            position: relative;
            .markdown-body{
                height: 900px;
            }
        }
    }

</style>
